*
{
    margin: 0;
    padding: 0;
}
.cont-home
{
    width: 100%;
    height: 100vh;
    background-image: url("https://www.shutterstock.com/image-photo/supermarket-aisle-product-shelves-interior-260nw-1479492230.jpg");
    background-repeat: no-repeat;
    background-size: cover;
}
.content-home
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 85vh;
}
.content-home .cont-icon
{
    font-size: 50px;
    font-weight: 800;
    color: brown;
}
.content-home h1
{
    font-size: 75px;
}
.content-home button
{
    cursor: pointer;
    border: none;
    outline: none;
    border-radius: 20px;
    width:200px;
    background-color: darkgreen;
    padding: 10px 5px;
    font-size: 20px;
    font-weight: 600;
    margin-top: 20px;
}
.content-home button:hover
{
    background-color: goldenrod;
    font-size: 21px;
}
.content-home a
{
    text-decoration: none;
    color: white;
}