@import url('https://fonts.googleapis.com/css2?family=Londrina+Solid&display=swap');
*
{
    margin:0;
    padding: 0;
    box-sizing: border-box;
}
.cont-pi
{
    display: flex;
    justify-content:flex-start;
    gap:45px;
    font-family: 'Londrina Solid', sans-serif;
}
.container-pi
{
    width: 35%;
    height: 380px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
}
.container-pi img
{
    margin:10px;
    width:400px;
    height:350px;
}
.content-pi
{
    width:60%;
    height: 380px;
    margin-top: 20px;
    background-color: rgb(219, 221, 224);
    box-shadow: 0px 6px 15px 0px rgba(26, 25, 25, 0.75);
    
}
.content-pi h1
{
    font-weight: 700;
    margin: 20px;
}
.content-pi h2
{
   font-size: 30px;
   font-weight: 700;
   margin: 20px;
}
.content-pi h3
{
    font-size: 25px;
   font-weight: 700;
   margin: 20px;
}
.content-pi button
{
    width: 300px;
    margin-top: 20px;
    margin-left: 20px;
    font-size: 25px;
    padding: 10px;
    font-weight: 700;
    cursor: pointer;
    border: none;
    outline: none;
    border-radius: 20px;
    background-color: rgb(199, 196, 3);
}
