@import url('https://fonts.googleapis.com/css2?family=Bree+Serif&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Londrina+Solid&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap');
*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container
{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    padding: 5px;
    align-items: center;
    box-shadow: 0px 6px 15px 0px rgba(26, 25, 25, 0.75);
    background-color: rgb(190, 186, 186);
}
.title
{
    width: 30%;
    margin-left: 50px;
    display: flex;
    align-items: center;
    gap:10px;
    font-family: 'Bree Serif', serif;
}
.title img
{
    width: 60px;
    height: 50px;
}
.title h3
{
    font-size: 22px;
    font-weight: 700;

}
.title span
{
    color: rgb(59, 1, 1);
    font-family: 'Londrina Solid', sans-serif;
    font-size: 30px;
    letter-spacing: 1px;
}
.nav
{
    width: 55%;
    height: 100%;
    
}
.nav ul
{
    display: flex;
    align-items: center;
    gap:25px;
    list-style-type: none;
    
}
.nav ul li a
{
    color: rgb(2, 1, 39);
    text-decoration: none;
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    font-weight: 700;
    cursor: pointer;  
    transition: 0.5s ease-in-out;
}
.nav ul li a:hover
{
    font-size: 22px;
    transform: scale(1.1);
    color: rgb(209, 6, 6)
}
.btn 
{
    width: 25%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
}
.btn button
{
    width: 100px;
    font-family: 'Roboto', sans-serif;
    font-size: 15px;
    font-weight: 700;
    padding: 5px;
    border: none;
    outline: none;
    border-radius: 25px;
    cursor: pointer;
    background-color: rgb(4, 61, 1);
    color: white;

}
.btn button:hover
{
    background-color: aquamarine;
    color: black;
    font-size: 18px;
}
.btn a
{
    font-size: 20px;
    font-weight: 700;
    color: rgb(2, 1, 39);
    cursor: pointer;
}