@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap');
*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.cont-user
{
    width: 100%;
    height:100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: 'Roboto', sans-serif;
}
.content-user h1
{
    margin-top: 40px;
    font-size: 35px;
    font-weight: 700;
    text-decoration: underline;
    font-family: 'Roboto', sans-serif;
}


.content-user
{
    width:50%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.content-user input
{
    margin-top: 30px;
    width: 400px;
    height: 40px;
    padding: 10px 20px;
    font-size: 20px;
    font-weight: 500;
    border: none;
    outline: none;
    border-bottom: 1px solid black;
    background-color:transparent;
    font-family: 'Roboto', sans-serif;
}
.content-user textarea
{
    width: 400px;
    height: 100px;
    padding: 10px 20px;
    font-size: 20px;
    font-weight: 500;
    border: none;
    outline: none;
    border-bottom: 1px solid black;
    background-color: transparent;
    font-family: 'Roboto', sans-serif;
}
.btn-user button
{
    width: 300px;
    margin-top: 30px;
    font-size: 25px;
    font-family: 'Roboto', sans-serif;
    padding: 10px;
    font-weight: 700;
    cursor: pointer;
    border: none;
    outline: none;
    border-radius: 20px;
    background-color: rgb(199, 153, 3);
}
.btn-user button:hover
{
    background-color: darkgreen;
    color: white;
    font-size: 26px;
}