*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container-cart
{
    width: 100%;
}
.content-cart
{
    width: 80%;
    margin-left: 100px;
    margin-top:50px; 
    padding:10px;   
    display: flex;
    justify-content: center;
    align-items: center;
    gap:150px;
    background-color: rgb(32, 112, 16);
    color: white;
    box-shadow: 10px 10px 5px 0px rgba(143,137,137,0.75);
}
.content-cart img
{
    width:75px;
}
.content-cart button
{
    width: 120px;
    margin-top: 10px;
    font-size: 15px;
    font-weight:800;
    color: white;
    padding: 5px;
    font-weight: 500;
    cursor: pointer;
    border: none;
    outline: none;
    border-radius: 20px;
    background-color: rgb(253, 4, 4);
}
.nil
{
    font-size: 30px;
    font-weight: 700;
    text-align: center;
    margin-top: 50px;
}