*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container-cart
{
    width:100%;
    margin-top: 10px;
   display: flex;
   justify-content: center;
   gap:25px;
   box-shadow: 0px 6px 15px 0px rgba(26, 25, 25, 0.75);
}
.cont-cart
{
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center
}
.cont-cart img
{
    width:200px;
    height:200px;
}
.data-cart
{
    width: 60%;
   display: flex;
   justify-content:space-around;
   align-items: center;
}
.data-cart button
{
    cursor: pointer;
    background-color: red;
    padding: 5px;
    border: none;
    outline: none;
    border-radius: 30px;
    font-size: 20px;
    width: 100px;
    color: white;
}
.data-cart .btn-buy
{
    cursor: pointer;
    background-color: rgb(14, 95, 3);
    padding: 5px;
    border: none;
    outline: none;
    border-radius: 30px;
    font-size: 20px;
    width: 120px;
    color: white;
}
