*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container-login
{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.contain-login
{
    width: 30%;
    height: 60vh;
    border: 3px solid black;
    border-radius:40px;
    margin-bottom: 40px;
    box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
    background-color: rgb(190, 186, 186);
}
.contain-login h1
{
    font-size:35px;
    font-weight: 700;
    text-align: center;
    text-decoration: underline;
    margin-top: 10px;
}

.content-login
{
    margin-top: 25px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.content-login input
{
    width:350px;
    height:30px;
    padding: 20px 20px;
    margin-top: 20px;
    border-radius: 10px;
    border: 1px solid black;
    outline: none;
    font-size: 16px;
}
.btn-sl
{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap:10px;
}
.btn-sl button
{
    width:120px;
    padding: 5px 10px;
    border: none;
    outline: none;
    background-color: rgb(9, 1, 37);
    border-radius: 25px;
    margin-top: 10px;
    cursor: pointer;
    font-size: 20px;
    font-weight: 600;
    color: white
}
.btn-sl button:hover
{
    font-size: 22px;
    background-color: black;
}