*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container-prod
{
    width:100%;

}
.images
{
    width:80%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.images img
{
    width:150px;
    height: 100px;
    margin-top:20px ;
    cursor: pointer;
}

.images h2
{
    margin-top: 10px;
    font-size: 15px;
}
.images button
{
    width:120px;
    height: 30px;
    margin-top: 20px;
    padding: 5px,10px;
    background-color: goldenrod;
    border: none;
    outline: none;
    cursor: pointer;
    font-size: 18px;
    border-radius:30px;

}